body {
  background-color: rgb(122, 122, 122);
}

.App {
  text-align: center;
}

.perfil {
  border-radius: 10%;

  /* -webkit-box-reflect: below 1px
    linear-gradient(transparent, transparent, rgba(0, 0, 0, 0.4)); */
  height: 25vmin;
  padding: 7px;
  transition: all 0.5s ease;
}

.perfil:hover {
  transform: scale(1.1);
}

.scale:hover {
  transform: scale(1.1);
}

.linkHover:hover {
  color: #a7a7a7;
  transform: scale(1.1);
}

.link-hover:hover {
  transform: scale(1.02);
}

.presentation {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  font-size: 0.9rem;
  gap: 10px;
  justify-content: space-around;
  margin: 10px auto;
  margin-top: 28vmin;
  width: 95%;
}

.projects {
  margin-top: 28vmin;
}

.myArticle {
  background-color: #afafaf;
  border-radius: 10px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  height: fit-content;
  max-width: 700px;
  padding: 10px;
  z-index: 1;
}

.stacksArticle {
  background-color: #afafaf;
  border-radius: 10px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  height: fit-content;
  max-width: 1050px;
  padding: 10px;
  z-index: 1;
}

p {
  line-height: 1.3;
}

.myHabilities {
  padding: 10px;
}

.hability-list {
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
  justify-content: space-evenly;
  padding: 0;
}

.stacks {
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  justify-content: space-evenly;
  padding: 10px;
}

.stackCard {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.project-list, .hability-list {
  list-style-type: none;
  padding-left: 0;
}

.App-header {
  background-color: #282c34;
  color: white;
  display: flex;
  font-size: calc(10px + 2vmin);
  justify-content: flex-start;
  line-height: 0.1;
  min-height: 25vh;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 5;
}

.navLink {
  border-right: 1px solid rgba(0, 0, 0, 0.664);
  color: #858ed8;
  font-size: 0.8rem;
  line-height: 1;
  padding: 20px;
  text-align: left;
  text-decoration: unset;
  width: 21vmin;
}

a {
  color: unset;
  text-decoration: unset;
}

.hello {
  align-self: center;
  padding: 10px;
  width: 80%;
}

.card {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  margin-bottom: 25px;
}

.project-card {
  background-color: rgb(114, 114, 114);
  border-radius: 30px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  color: rgb(209, 207, 207);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 7px;
  width: 30%;
  z-index: 1;
}

@media ( prefers-reduced-motion : no-preference ) {

  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {

  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.imageList {
  border-radius: 10px;
  width: 100px;
}

.tech {
  text-shadow: #282c34 1px 1px 1px;
}
